.section__overlay {
	display: none;
	height: 100vh;
	width: 100vw;
	align-items: center;
	justify-content: center;
	z-index: 20;
	position: fixed;
	top: 0;
	left: 0;
	span.overlay {
		height: 100vh;
		width: 100vw;
		background-color: $black-6;
		position: absolute;
	}
}
.form {
	width: 40vw;
	height: max-content;
	animation: formFadeUp 0.35s cubic-bezier(0.17, 0.67, 0.59, 1.76);
	position: relative;
	margin: 0 auto;
	padding: 5rem;
	padding-bottom: 7rem;
	box-shadow: $shadow-lightest;
	border-radius: 0.5rem;
	background-image: $gradient-convex,
		linear-gradient(135deg, $color-grey, $color-grey);
	&__header {
		font-size: 2.5rem;
		letter-spacing: 2px;
		background-image: linear-gradient(135deg, $color-1, $color-4);
		background-clip: text;
		-webkit-background-clip: text;
		color: transparent;
		margin: 3rem;
		font-weight: 700;
		text-align: center;
	}
	&__input {
		padding: 1.5rem 2rem;
		font-family: inherit;
		width: 100%;
		color: $color-3;
		background-color: $color-grey;
		font-weight: 400;
		font-size: 1.6rem;
		margin: 2rem auto;
		border: none;
		border-radius: 3rem;
		box-shadow: $shadow-neu-inset;
		transition: all 0.3s;
		&:focus {
			outline: none;
			box-shadow: $shadow-neu-inset, $shadow-purple-glow, $shadow-blue-glow;
		}
		&::-webkit-input-placeholder {
			font-size: 2rem;
			color: $black-6;
		}
		&:-moz-placeholder {
			font-size: 2rem;
			color: $black-6;
		}
		&::-moz-placeholder {
			font-size: 2rem;
			color: $black-6;
		}
	}
	&__input::placeholder {
		font-size: 1.6rem;
		color: $black-6;
	}
	&__buttons {
		display: flex;
		align-items: center;
		justify-content: space-between;
		&--main {
			display: flex;
			flex: 0.5;
			justify-content: space-between;
		}
	}
	#show-password {
		color: $black-4;
		cursor: pointer;
		transform: translateY(1rem);
	}
	.btn-form {
		cursor: pointer;
		font-size: 1.6rem;
		letter-spacing: 1.6px;
		background-color: $color-2;
		color: $white;
		border: none;
		font-family: inherit;
		font-size: 1.6rem;
		border-radius: 0.3rem;
		padding: 1.5rem 2.5rem;
		box-shadow: $shadow-neu-small, $shadow-bevel;
		margin-right: 2rem;
		margin-top: 2rem;
		transition: 0.2s ease-in-out;
		&:not(:last-child) {
			margin-right: 4rem;
		}
		&:hover {
			box-shadow: $shadow-neu-small, $shadow-concave;
		}
		&:active {
			transform: scale(0.95);
			outline: none;
			border: none;
		}
		&:focus {
			outline: none;
			border: none;
		}
	}
}
.close-button {
	padding: 3rem 3.5rem;
	visibility: hidden;
	border-radius: 50%;
	background-image: $gradient-convex, linear-gradient(to right, $white, $white);
	font-size: 2.5rem;
	font-weight: 700;
	cursor: pointer;
	color: $color-1;
	box-shadow: $shadow-lightest;
	position: absolute;
	top: 0;
	right: 0;
	transform: translate(50%, -50%);
	animation: jump-x 0.8s;
	animation-fill-mode: forwards;
	animation-delay: 0.5s;
}
